export const login = ({ dispatch }, user) => {
  dispatch('SETUSER', user)
}

export const logout = ({ dispatch }) => {
  dispatch('CLEARUSER')
  document.cookie = "cookie=''"
}

export const cache = ({ dispatch }, blogs) => {
  dispatch('CACHEBLOGS', blogs)
}



