import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  user: '',
  blogs: ''
}

const mutations = {
  SETUSER (state, user) {
    state.user = user
  },
  CLEARUSER (state) {
    state.user = ''
  },
  CACHEBLOGS (state, blogs) {
    state.blogs = blogs
  }
}

export default new Vuex.Store({
  state,
  mutations
})



